import React from "react";

import Contacts from './components/contact/Contacts';
import Mainpage from './components/HomePage'
import Login from './components/user/Userslogin'
import Signup from './components/user/userSignup'
import Addnew from './components/contact/Addnew'
import {Route,Switch,Redirect} from 'react-router-dom';
import Update from "./components/contact/Update";

const App = () => {
  return (
    <>
    <Switch>
      <Route exact path="/" component={Mainpage}></Route>
      <Route path="/contact" component={Contacts}></Route>
      <Route path="/login" component={Login}></Route>
      <Route path="/signup" component={Signup}></Route>
      <Route path="/addnew" component={Addnew}></Route>
      <Route path="/update/:id" component={Update}></Route>
      <Redirect to="/" />
      </Switch>
    </>
  );
};

export default App;