import { React } from "react";
import "./ContactCard.css";
import {useHistory} from 'react-router-dom'
import EditIcon from "@material-ui/icons/Edit";
import Button from "@material-ui/core/Button";
import DeleteForeverIcon from "@material-ui/icons/DeleteForever";
import PersonOutlineTwoToneIcon from "@material-ui/icons/PersonOutlineTwoTone";

const ContactCard = ({ name, email, phone, id, handleClick }) => {
  const history = useHistory();
  return (
    <div className="Contact_Card">
      <div className="Contact_Card_detail">
        <div className="Contact_Card_rapper">
          <div className="logo">
            <PersonOutlineTwoToneIcon style={{ fontSize: 50 }} />
          </div>
          <h3 className="Contact_rapper_name">{name}</h3>
          <h3 className="Contact_rapper_email">{email}</h3>
          <h3 className="Contact_rapper_phone">{phone}</h3>
          <div className="Contact_upgrade">
            <Button
              onClick={() => {
                history.push(`/update/${id}`);
              }}
              variant="contained"
              type="submit"
              color="primary"
            >
              <EditIcon />
            </Button>
            <Button
              onClick={() => handleClick(id)}
              variant="contained"
              type="submit"
              color="primary"
            >
              <DeleteForeverIcon />
            </Button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ContactCard;
