import React from "react";
import { useForm } from "react-hook-form";
import axios from "axios";
import "./addnew.css";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import loginImg from "../user/Asset/sideimg.png";
import loginlogo from "../user/Asset/logo.png";
import { useHistory } from "react-router-dom";

const Addnew = () => {
  const history = useHistory();
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();
  const onsubmit = (data) => {
    console.log(data);
    axios
      .post("/contact/post", data, {
        withCredentials: true,
      })
      .then((res) => {
        console.log(res);
        alert("created new contact");
        history.push("/contact");
      })
      .catch((err) => {
        console.log(err);
        alert("error creating new contact");
      });
  };

  return (
    <>
      <div className="signupCardMain">
        <div className="signupImage">
          <img src={loginImg} alt="signupimg" />
        </div>
        <div className="signupDetailsForm">
          <div className="signupCard">
            <img className="signuplogo" src={loginlogo} alt="logosignup" />
            <br />
            <form onSubmit={handleSubmit(onsubmit)}>
              <div className="signupTextfield">
                <TextField
                  {...register("name", { required: true })}
                  name="name"
                  id="name"
                  label="Name"
                  type="text"
                  variant="outlined"
                />
                {errors.name && alert("All the fields are required")}
              </div>
              <div className="signupTextfield">
                <TextField
                  {...register("email")}
                  name="email"
                  id="email"
                  label="Email"
                  type="email"
                  variant="outlined"
                />
              </div>
              <div className="signupTextfield">
                <TextField
                  {...register("phone")}
                  name="phone"
                  id="phone"
                  label="phone"
                  type="Number"
                  variant="outlined"
                />
              </div>
              <Button variant="contained" type="submit" color="primary">
                Add New
              </Button>
            </form>
          </div>
        </div>
      </div>
    </>
  );
};

export default Addnew;
