import React from "react";
import { Link } from "react-router-dom";
import './HomePage.css'

const HomePage = () => {
  return (
    <>
    <div className="ContentBg">
    <div className="MainTextBg">
    <div className="TextBg">
      <div className="MainText">
      <h1>Welcome to Your Personal Contact Log</h1>
      <div className="MainText_Content">
        <h3>New to Our portal ?</h3>
        <Link to="/signup"><button type="button"><p>sign-up</p></button></Link>
      
        <h3>Are you an Existing User ?</h3>
        <Link to="/login"><button type="button"><p>log-in</p></button></Link>
        </div>
      </div>
      </div>
      </div>
      </div>
    </>
  );
};

export default HomePage