import React from "react";
import { useForm } from "react-hook-form";
import axios from "axios";
import "./addnew.css";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import loginImg from "../user/Asset/sideimg.png";
import loginlogo from "../user/Asset/logo.png";
import { useHistory, useParams } from "react-router-dom";

const Update = () => {
  let {id} = useParams();
  const history = useHistory();
  const { register, handleSubmit,formState: { errors } } = useForm();
  const onsubmit = (data) => {
    axios
      .patch(`/contact/update/${id}`, data, {
        withCredentials: true,
      })
      .then((res) => {
        alert("successfully updated");
        history.push("/contact");
      })
      .catch((err) => {
        console.log(err);
        alert("error updating data");
      });
  };

  return (
    <>
      <div className="signupCardMain">
        <div className="signupImage">
          <img src={loginImg} alt="signupimg" />
        </div>
        <div className="signupDetailsForm">
          <div className="signupCard">
            <img className="signuplogo" src={loginlogo} alt="logosignup" />
            <br />
            <form onSubmit={handleSubmit(onsubmit)} >
              <div className="signupTextfield">
                <TextField
                  {...register("name", { required: true })}
                  name="name"
                  id="name"
                  label="Name"
                  type="text"
                  variant="outlined"
                  
                />
                {errors.name && alert("All the fields are required")}
              </div>
              <div className="signupTextfield">
                <TextField
                  {...register("email", { required: true })}
                  name="email"
                  id="email"
                  label="Email"
                  type="email"
                  variant="outlined"
                />
                
              </div>
              <div className="signupTextfield">
                <TextField
                  {...register("phone", { required: true })}
                  name="phone"
                  id="phone"
                  label="phone"
                  type="Number"
                  variant="outlined"
                />
                
              </div>
              <Button variant="contained" type="submit" color="primary">
                Update
              </Button>
            </form>
          </div>
        </div>
      </div>
    </>
  );
};

export default Update;
